import axios from "axios"

export default {
    actions: {
        async userLogin({dispatch, commit}, data){
            console.log("axios "+data.login)
        axios.get('/', {
            params: {
            userLogin: data,
            }
            })
            .then(function (response) {
                if(response.data['error'] == null){
                    console.log("res data "+ response.data.login)
                    commit("setUserData", response.data)
                    dispatch('GetTodo', null, { root: true }) 
                }else {
                    commit("setError", {status: true, message: response.data['error']})
                }
            
            })
            .catch(function (error) {
            console.log(error);
            });
       },
       async userReg({dispatch, commit}, data){
        axios.get('/', {
            params: {
            userReg: data,
            }
            })
            .then(function (response) {
                commit("regStatus", response.data)
                dispatch('checkStatus', null, { root: true }) 
                
            })
            .catch(function (error) {
            console.log(error);
            });
       },

       async checkStatus({dispatch, commit}, data){
            console.log("checkStatus" + data)
            let token = localStorage.getItem("token");
            axios.get('/', {
                params: {
                checkUser: token,
                }
                })
                .then(function (response) {
                    if(response.data != "Bad token"){
                        
                        commit("setUserData", response.data)
                        dispatch('GetTodo', null, { root: true }) 
                    }
                    
                })
                .catch(function (error) {
                console.log(error);
                });
                
           
       },

    },
    mutations: {
        changeStatus(state){
            state.userStatus = !state.userStatus;
        },
        setUserData(state, data){
            console.log("SetData")
            state.userData = data;
            state.token = data['token'];
            localStorage.setItem("token", data['token']);
            state.userStatus = true;
        },
        regStatus(state, data){
            state.regStatus = data;
            state.token = data;
            localStorage.setItem("token", data);

        },
        logOut(state) {
            state.userData = [];
            state.userStatus = false;
            state.token = "";
            state.errorMessage = {};
            localStorage.setItem("token", "");
        },
        setError(state, data){
            state.errorMessage = data;
        },
       
    },
    state: {
        userStatus: false,
        userData: [],
        regStatus: [],
        token: "",
        errorMessage: {},

        
    },
    getters: {
        getStatus(state){
            return state.userStatus;
        },
        getUserData(state){
            return state.userData;
        },
        getRegStatus(state){
            return state.regStatus;
        },
        getErrorMessage(state){
            return state.errorMessage;
        },
       
    },

 

}