import axios from "axios"


export default {

    actions: {
        async fetchPost(ctx, num=3){
            // const res = await fetch(
            //     "http://jsonplaceholder.typicode.com/posts?_limit="+num
            // );
            //const posts = await res.json();
            axios.get('http://vuetest/', {
                params: {
                post: num,
              }
              })
              .then(function (response) {
                ctx.commit("updatePosts", response.data)
              })
              .catch(function (error) {
                console.log(error);
              });


        },

        async addPost(ctx, post){
            var postItem = post;
            axios.get('http://vuetest/', {
                params: {
                newPost: post,
              }
              })
              .then(function (response) {
                postItem['id'] = ""+response.data;
                ctx.commit("addPost", postItem);
              })
              .catch(function (error) {
                console.log(error);
              });
             
        },

        async removePost(ctx, id){
            let temp = id;
            axios.get('http://vuetest/', {
                params: {
                removePost: id,
              }
              })
              .then(function () {
                ctx.commit("removePost", temp);
              })
              .catch(function (error) {
                console.log(error);
              });
        }
    },
    mutations: {
        updatePosts(state, posts){
            state.posts = posts.reverse();
        },
        addPost(state, post){
            state.posts.unshift(post)
        },
        removePost(state, id) {
            state.posts = state.posts.filter(t => t.id !== id);
        }
    },
    state: {
        posts: [],
    },
    getters: {
        allPosts(state){
            return state.posts
        },
        getLengthPosts(state){
            return state.posts.length;
        }
    },

}