import axios from "axios"

export default ({


    actions: {
        async GetTodo(ctx){
            console.log("this data "+ctx.rootState.user.userData.id)
            if(ctx.rootState.user.userStatus){
                console.log("2")
                axios.get('/', {
                    params: {
                        todo: ctx.rootState.user.userData.id,
                    }
                    })
                    .then(function (response) {
                        ctx.commit("getTodo", response.data)
                        
                    })
                    .catch(function (error) {
                    console.log(error);
                    });
            }
           },
           async PushTodo(ctx, data){
            axios.get('/', {
                params: {
                    newTodo: data[0],
                    idUser: data[1],
                }
                })
                .then(function (response) {
                    ctx.commit("getTodo", response.data)
                    
                })
                .catch(function (error) {
                console.log(error);
                });
           },
           async CreateTodo(ctx, data){
            axios.get('/', {
                params: {
                    typeTodo: data[0],
                    idUser: data[1],
                }
                })
                .then(function (response) {
                    ctx.commit("getTodo", response.data)
                    
                })
                .catch(function (error) {
                console.log(error);
                });
           }
    },
    mutations: {
        getTodo(state, data){
            console.log("4")
            state.todoList = data;
            let json_todo = JSON.parse(data);
            let temp = Object.keys(json_todo);
            state.todoKey = [];
            state.todoId = temp;
            temp.forEach(element => {
                state.todoKey.push(json_todo[element][0])
            });
            if(state.todoName != null) {
                state.todoArray = json_todo[state.todoName]
            }

        },
        updateTodo(state, data){
            state.todoList = data;
        },

        updateTodoName(state, data){
            console.log("name is " +data)
            state.todoName = data
        }

    },
    state: {
        todoList: {},
        todoKey: [],
        todoId: [],
        todoName: null,
        todoArray: [],
    },
    getters: {
        getTodoList(state){
            console.log("3")
            return state.todoList;
        },
        getTodo(state){
            
            return state.todoKey.reverse();
        },
        getTodoId(state){
            return state.todoId.reverse();
        },
        getTodoArray(state){
            return state.todoArray;
        }
    },

   

})